          btn.value = "Stop";
          title.innerText = "";
          timer = setInterval(function () {
            // Math.ceil å‘ä¸Šå–æ•´
            // Math.random() æ–¹æ³•å¯è¿”å›ä»‹äº 0 ~ 1 ä¹‹é—´çš„ä¸€ä¸ªéšæœºæ•°
            let props = Math.ceil(Math.random() * lollipop.length),
              propsTop = Math.ceil(
                Math.random() *
                (window.document.getElementById("container").offsetHeight -
                  48) +
                48
